import { Component } from '@angular/core';
import { Storage } from '@ionic/storage';
import { HangarPage } from '../hangar/hangar';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the BattlePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-battle',
  templateUrl: 'battle.html',
})
export class BattlePage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage) {
  }

  ionViewDidLoad() {
  setTimeout(() => {
  var planet = this.navParams.get('planet');
  var level = this.navParams.get('level');

  // Check how many pets the player has and place them on the screen
  this.storage.get('Profile').then((data) => {
  
  var profileInfo = JSON.parse(data); 
 
  //document.getElementById("battlemain").innerHTML = '<div id="battlebg" style="background-image: url(\'/assets/planets/' + planet + '.png\');"></div>';
  if(profileInfo["Pet1"] == "Yes"){
  this.storage.get('PetOneInfo').then((data1) => {
  var petInfo1 = JSON.parse(data1);
  document.getElementById("pokeBattle1").innerHTML = '<img id="pokeSprite1" src="../assets/combat/' + petInfo1["sprite"] + '.png" style="width: 70px; height: 70px;" />';
   });  
  }

  if(profileInfo["Pet2"] == "Yes"){
  this.storage.get('PetOneInfo').then((data2) => {
  var petInfo2 = JSON.parse(data2);
  document.getElementById("pokeBattle2").innerHTML = '<img id="pokeSprite1" src="../assets/combat/' + petInfo2["sprite"] + '.png" style="width: 70px; height: 70px;" />';
   });   
  }

  if(profileInfo["Pet3"] == "Yes"){
  this.storage.get('PetOneInfo').then((data3) => {
  var petInfo3 = JSON.parse(data3);
  document.getElementById("pokeBattle3").innerHTML = '<img id="pokeSprite1" src="../assets/combat/' + petInfo3["sprite"] + '.png" style="width: 70px; height: 70px;" />';
   });   
  }
   });
   }, 400);  
  }
}



