import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicStorageModule } from '@ionic/storage';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { NewgamePage } from '../pages/newgame/newgame';
import { NewgamepetPage } from '../pages/newgamepet/newgamepet';
import { HangarPage } from '../pages/hangar/hangar';
import { ViewpetonePage } from '../pages/viewpetone/viewpetone';
import { BattlePage } from '../pages/battle/battle';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    NewgamePage,
    NewgamepetPage,
    HangarPage,
    ViewpetonePage,
    BattlePage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/viewpetone/viewpetone.module#ViewpetonePageModule', name: 'ViewpetonePage', segment: 'viewpetone', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/battle/battle.module#BattlePageModule', name: 'BattlePage', segment: 'battle', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/hangar/hangar.module#HangarPageModule', name: 'HangarPage', segment: 'hangar', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/newgamepet/newgamepet.module#NewgamepetPageModule', name: 'NewgamepetPage', segment: 'newgamepet', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/newgame/newgame.module#NewgamePageModule', name: 'NewgamePage', segment: 'newgame', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    NewgamePage,
    NewgamepetPage,
    HangarPage,
    ViewpetonePage,
    BattlePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    Storage
  ]
})
export class AppModule {}



